.scrollRoman {
    height: 100px;
    width: 300px;
    font-size: 100px;
    display: block;
    position: relative;
    -webkit-animation: scroll 10s linear infinite;
            animation: scroll 10s linear infinite;
    color: purple;
    z-index: 2;
    font-family: 'Myriad Pro', 'Myriad','Constantine', helvetica, arial, sans-serif;
    text-shadow: 2px 2px 3px white;
    
}

@-webkit-keyframes scroll{
    0% {left:800px;}
    100% {left:-800px;}
}

@keyframes scroll{
    0% {left:800px;}
    100% {left:-800px;}
}


