.input_box {
    position:fixed;
    width: 400px;
    height: 250px;
    top:50%;
    left:50%;
    margin-top: -125px;
    margin-left: -200px;
    display: block;
    color: white;
    border: 1px solid darkgrey;
    border-radius: 25px;
    background-color: hsla(0,0%,70%,0.40);
    z-index: 3;
}



